<template>
  <v-container>
    <v-card>
      <v-row>
        <v-col>
          <v-card-title>{{ event.etitle }}</v-card-title>
        </v-col>
        <v-col cols="3">
          <v-card-text><v-icon>mdi-list</v-icon></v-card-text>
        </v-col>
      </v-row>
      <v-img :src="event.eimgurl"></v-img>
      <v-card-title v-html="event.econtent"></v-card-title>
      <v-card-text
        >{{ new Date(event.estartdate).toLocaleString().substring(0, 12) }} ~
        {{ new Date(event.eenddate).toLocaleString().substring(0, 12) }}</v-card-text
      >
      <v-card-text align="center">
        <v-btn color="primary">쿠폰 발급</v-btn>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import EventApi from "@/apis/event";
export default {
  name: "EventDetail",
  data: () => ({
    event: {},
  }),
  methods: {
    async getEvent(eno) {
      try {
        const promise = await EventApi.eventDetail(eno);
        console.log(promise.data.event);
        this.event = promise.data.event;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getEvent(this.$route.query.eno);
  },
};
</script>

